import React from "react";
import "./TextMove.css";
import Quotes from "../../Files/Quotes";

function TextMove() {
  let i = 0;
  const [currentText, setcurrentText] = React.useState(Quotes[i]);

  console.log("hello" + currentText);

  React.useLayoutEffect((i) => {
    setInterval(() => {
      if (i <= Quotes.length - 1) {
        setcurrentText(Quotes[i]);
        i++;
        return;
      } else {
        i = 0;
        setcurrentText(Quotes[i]);
        i++;
        return;
      }
    }, 3000);
  }, []);

  return (
    <div className="dropText">
      <p>{currentText}</p>
    </div>
  );
}
export default TextMove;
